import React from 'react';
import D2 from "../assets/D5.jpg";


export const InfoDucks = () => {
    return <div className="info halfScreen mt-8" style={{backgroundImage: `url(${D2}`}}>
        <h1 className='text-4xl text-darkTXT mb-4'>Żywienie kaczek</h1>
        <div className='bg-green w-32 h-1 p-0.5 my-4 rounded-2xl'> </div>
        <p className='w-2/5 text-left text-sm mt-8 tracking-widest break-words text-justify text-darkTXT'>
            W żywieniu kaczek oprócz mieszanek pełnoporcjowych można również stosować pasze gospodarskie, 
zmniejszając koszty. W okresie wiosenno-letnim można stosować zielonki. Najbardziej przydatne i chętnie zdajane 
przez ptaki to młode zielonki z roślin motylkowatych- lucerny, wyki lub koniczyny lub zbóż- żyta lub owsa. 
Kaczki chętnie również pobierają rośliny takie jak szpinak, sałata, krwawnik, pokrzywa, komosa, kapusta pastewna. 
Zielonki powinny być rozdrobnione i świeże. W zimie aby zrekompensować brak zielonek można podawać kaczkom susz, 
kiszonki (lucerna, koniczyna, parowane ziemniaki) oraz okopowe. Kaczki od 35 dnia życia mogą dostawać ziemniaki 
gotowane lub parowane i marchew. Kaczki bardzo chętnie pobierają produkty uboczne przemysłu rolno-spożywczego 
takie jak wysłodki buraczane, serwatkę, maślankę, młóto browarniane, kiełki słodowe i drożdże, susz ziemniaczany oraz płatki.
Podstawą żywienia kaczek są ziarna zbóż, w mieszankach pełnoporcjowych stanowią do 40 % (najchętniej zjadane są kukurydza, 
pszenica, jęczmień), na drugim miejscu należy wymienić nasiona roślin strączkowych, powinny stanowić maksymalnie 15% składu dawki. 
Bardzo ważną rolę w mieszankach stanowią nasiona lnu oraz rzepaku, a także śruty poekstrakcyjne. W granulatach jako dodatek 
można spotkać produkty uboczne przemysłu tj melasa, wywar ziemniaczany lub zbożowy, wysłodki, ale również pasze objętościowe 
z traw czy roślin motylkowatych i okopowych.</p>
        <p className='text-blueTxt text-xs mt-4'>Materiał opublikowany na portalu rolniczym Rynek Rolny: 
	<a href="https://www.rynek-rolny.pl/artykul/zywienie-i-pasze-dla-kaczek-jak-wybrac-i-dopasowac-najlepsze.html?page=2">"Pasze w żywieniu kaczek"</a></p>
    </div>
}