import React from 'react';
import P2 from "../assets/P2.jpg";


export const InfoNumididae = () => {
    return <div className="info small mt-8" style={{backgroundImage: `url(${P2}`}}>
        <h1 className='text-4xl text-blueTxt mb-4'>Żywienie perlic</h1>
        <div className='bg-blueTxt w-32 h-1 p-0.5 my-4 rounded-2xl'> </div>
        <p className='w-2/5 text-left text-sm mt-8 tracking-widest font-light break-words text-justify'>
            W żywieniu perlic bardzo ważny jest bilans białka, tłuszczu, aminokwasów, witamin oraz składników mineralnych. 
W literaturze dotyczącej żywienia tych ptaków można się natknąć na zalecenia żywieniowe identyczne jak dla kur. 
Dominują pasze pochodzenia roślinnego- ziarna zbóż (pszenica, żyto jęczmień, owies, kukurydza), nasiona roślin motylkowatych, 
(groch, bobik, łubin), ześrutowane ziarna roślin oleistych (len, rzepak, słonecznik). Duży udział w paszy dla perliczek stanowią 
parowane lub gotowane ziemniaki, buraki i marchew. Perlice swobodnie poruszające się po wybiegach w okresie wiosenno-letnim
samodzielnie pobierają bardzo dużą ilość zielonek- pędów i liści, oraz pasze pochodzenia zwierzęcego, samodzielnie schwytane larwy, 
dżdżownice, chrabąszcze. W zimę należy im zapewnić dostęp do świeżej paszy objętościowej soczystej np. owoce, kapusta, cykoria, 
szczypior, marchewka. W zimę można uzupełniać brak owadów w paszy dodatkiem surowego mięsa do suchej paszy treściwej. W żywieniu 
stosuje się również mleko w różnej postaci, ryby, dodatki mineralne, drożdże. W żywieniu perlic bardzo ważny jest stały dostęp 
do świeżej i czystej wody. </p>
        <p className='text-blueTxt text-xs mt-4'>Materiał opublikowany na portalu rolniczym Rynek Rolny: 
	<a href="https://www.rynek-rolny.pl/artykul/hodowla-perlic-najwazniejsze-informacje.html">"Hodowla perlic - najważniejsze informacje"</a></p>
    </div>
}